@font-face {
    font-family: 'Poppins-Black';
    src: "./src/styles/fonts/Poppins-Black.ttf", "truetype", "Poppins";
}

body {
    background-color: rgb(200, 209, 240);
    color: #8781bd;
}

.main {
    text-align: center;
    scale: 85%;
    align-content: space-around;
    object-fit: contain;
}

.play-screen {
    display: flex;
    justify-content: center;
    max-width:100vw;
    padding: 1.5%;
    text-align: center;
    margin: 5px 0;
}

.header {
    display: flex;
    justify-content: center;
    align-content: space-between;
}

.logo {
    height: auto;
    text-align: left;
    margin-left: 25vw;
}

.logo img {
    width: 27%;
    height: auto;
    margin-top: 1%;
    padding-top: 1vw;
}

.logo img:hover {
    transition: .3s;
    animation: wiggle 2.5s;
    animation-fill-mode: forwards;
}

.links {
    height: auto;
    text-align: right;
    margin-right: 25vw;
    display: flex;
    align-content: space-between;
    flex-direction: row;
}

.links img {
    width: auto;
    height: 2.0vw;
    padding-top: 1.4vw;
    margin-left: 2vw;
}

.links img:hover {
    animation: zoomies 1s;
    animation-fill-mode: forwards;
    cursor: pointer;
    transition: .5s;
    filter: brightness(60%)
}

.links > img{
    margin-left: 10vw;
    margin-right: 2vw;
}

.audio-controls {
    font-family: "Poppins", sans-serif;
    max-width: 70vw;
    font-size: 20px;
    text-align: center;
    align-content: space-around;
    margin: auto;

}
.playback-controls div {
    display: inline-block;
    width: 15%;
}
.audio-controls img, .audio-controls div img {
    min-width: 1.5vw;
    max-width: 1.5vw;
    max-height: max-content;
    margin-left: 0.1vw;
    margin-right: 0.1vw;
}

.audio-controls #sound-button {
    display: none;
}

.audio-controls #mute-button {

    display: inherit;
}

.audio-controls img:hover {
    cursor: pointer;
    transition: .5s;
    filter: brightness(60%)
}

#background, #intro1, #intro2a, #intro2b, #intro2c, #intro3, #intro4 {
    background-color: #8781bd;
}
#background {
    display: none;
    animation: fadeIn 1s;
}

#intro-blank {
    display: none;
    animation: fadeIn 1s;
    background-color: #8781bd;
}

#intro1 {
    display: flex;
    animation: fadeIn 1s;
    background-color: #8781bd;
}

#intro2a {
    display: none;
    animation: fadeIn 1s;
    background-color: #8781bd;
}

#intro2b {
    display: none;
    animation: fadeIn 1s;
    background-color: #8781bd;
}

#intro2c {
    display: none;
    animation: fadeIn 1s;
    background-color: #8781bd;
}

#intro3 {
    display: none;
    animation: fadeIn 1s;
    background-color: #8781bd;
}
#intro4 {
    display: none;
    animation: fadeIn 1s;
    background-color: #8781bd;
}


@keyframes fadeIn {
    0% {
        filter: brightness(.0);
    }
    100% {
        filter: brightness(1);
    }
}

@keyframes fadeOut {
    0% {
        opacity: 100%;
    }

    100% {
        opacity: 0%;
    }
}

@keyframes wiggle {
    0% { transform: rotate(0deg), scale(100%) }
    14% { transform: scale(110%) }
    20% { transform: scale(105%) }
    24% { transform: rotate(-5deg); }
    28% { transform: rotate(5deg); }
    32% { transform: rotate(-5deg); }
    36% { transform: rotate(5deg); }
    30% { transform: rotate(-5deg); }
    44% { transform: rotate(0deg); }
    48% { transform: scale(105%); }
    54% { transform: scale(105%); }
    70% { transform: scale(100%); }
    100% { transform: scale(100%); }
}

@keyframes zoomies {
    0% {
        transform: scale(100%)
    }
    100% {
        transform: scale(110%);
    }
}